* {
  box-sizing: border-box;
}

.app {
  font-family: "Varela Round", sans-serif;
}

.fill-screen {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
}

.login-page h1 {
  text-align: center;
  margin-top: 35vh;
  margin-bottom: 70px;
}

#signinButton > div {
  margin: 20px auto;
}

.camera-view {
  background: #2a1102;
  position: relative;
}

.camera-view canvas {
  width: 100%;
  height: 100%;
  display: block;
  transform: scaleX(-1);
}

.camera-view .avatar {
  box-sizing: border-box;
  height: 10vw;
  width: 10vw;
  border-radius: 24vw;
  border: 2px solid #f4971e;
  position: absolute;
  top: 5vw;
  left: 5vw;
  margin: 0 auto;
  background: #f4971e;
}

.camera-view .shutter {
  box-sizing: border-box;
  height: 22vw;
  width: 22vw;
  border-radius: 50%;
  border: 5px solid #f4971e;
  position: absolute;
  bottom: 20vw;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.125);
  outline: none;
}

.effect-slider {
  position: absolute !important;
  bottom: 22vw;
  left: 0;
  width: 100%;
}

.effect-thumb {
  height: 18vw;
  width: 18vw;
  border: 6px solid white;
  border-radius: 50%;
  vertical-align: middle;
  text-align: center;
  color: white;
  background-size: 90%;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0 auto;
}

.camera-view .flip {
  position: absolute;
  top: 5vw;
  right: 5vw;
  height: 10vw;
  width: 10vw;
}

.camera-view .stories {
  position: absolute;
  bottom: 2vw;
  left: 42vw;
  height: 16vw;
  width: 16vw;
}

.camera-view .shutter:active {
  background: rgba(255, 255, 255, 0.5);
}

.edit-page .cancel,
.send-page .cancel,
.profile-page .cancel {
  position: absolute;
  top: 5vw;
  left: 5vw;
  height: 10vw;
  width: 10vw;
}

.edit-page button.send {
  position: absolute;
  bottom: 10vw;
  right: 5vw;
  height: 16vw;
  width: 16vw;
  background: #f4971e;
  border-radius: 20vw;
  box-shadow: 0 0 4vw black;
}

.edit-page button.send img {
  vertical-align: middle;
  display: inline-block;
  text-align: center;
  width: 70%;
  height: 70%;
}

.edit-page .sendTo {
  position: absolute;
  bottom: 4vw;
  right: 3vw;
  height: 4vw;
  width: 20vw;
}

.send-page {
  background: black;
  color: white;
  padding: 5vw;
  padding-top: 50px;
}

.send-page ul {
  background: white;
  color: black;
  border-radius: 10px;
  padding: 0;
}

.send-page ul li {
  list-style-type: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 12px 24px 10px 10px;
}

.send-page ul li:active {
  background: #ddd;
}

.send-page ul li .icon {
  height: 28px;
  width: 28px;
  border-radius: 14px;
  background: black;
  display: inline-block;
  margin-right: 10px;
  background-size: cover;
}

.send-page ul li .label {
  display: inline-block;
  position: relative;
  top: -6px;
}

.send-page ul li .chevron {
  float: right;
  margin-top: 4px;
}

.send-page .sendingModal {
  position: fixed;
  width: 70vw;
  height: 80vh;
  top: 10vh;
  left: 15vw;
  background: white;
  color: black;
  border-radius: 10px;
  box-shadow: 0 0 40px black;
  text-align: center;
  font-size: 10vw;
  padding-top: 32vh;
}

.profile-page {
  background: #f7f8f9;
  padding: 20vw 5vw;
}

.profile-page > div > img {
  border-radius: 20px;
  border: 2px solid black;
}

.profile-page h3 {
  font-size: 14px;
  margin-top: 28px;
}

.profile-page .cancel {
  filter: brightness(0%);
}

button {
  box-sizing: border-box;
  outline: none;
  border: none;
  background: transparent;
}

button img {
  width: 100%;
  height: 100%;
  display: block;
}

.floating-label {
  position: absolute;
  font-size: 4vw;
  color: #f4971e;
  text-align: center;
  font-weight: bold;
  text-shadow: 0 0 3px black;
}

.row-button {
  border: 1px solid #eee;
  border-radius: 10px;
  padding: 12px 24px 10px;
  font-weight: bold;
  display: block;
  margin: 0 0 10px 0;
  text-decoration: none;
  color: black;
  box-shadow: 0 0 5px #ccc;
}
